/**
 *  
 * @param arr 
 * @param size 
 */
const chunk = (arr, size) =>
    Array.from({ length: Math.ceil(arr.length / size) }, (v, i) =>
        arr.slice(i * size, i * size + size)
    );




/**
 * css
 * @return css
 */
// let elementStyle = document.createElement('div').style
let vendor = (() => {
    // let transformNames = {
    //     'webkit': 'webkitTransform',
    //     'Moz': 'MozTransform',
    //     'O': 'OTransform',
    //     'ms': 'msTransform',
    //     'standard': 'transform'
    // }
    // for (let k in transformNames) {
    //     if (elementStyle[transformNames[k]] !== undefined) {
    //         return k
    //     }
    // }
    return false
})()
const prefix = (style) => {
    if (!vendor) { //return
        return false
    }
    if (vendor === 'standard') {
        return style
    }
    return vendor + style.charAt(0).toUpperCase() + style.substr(1)
}

/**
 * ()
 * @param arr 
 * @return Array 
 */
const shuffle = function(arr) {
    let _arr = arr.slice() //
    for (let i = 0; i < _arr.length; i++) {
        let j = getRandomInt(0, i)
            // 
        let t = _arr[i]
        _arr[i] = _arr[j]
        _arr[j] = t
    }
    return _arr
}

/**
 * 
 * @param min 
 * @param max 
 * @return 
 */
const getRandomInt = function(min, max) {
    return (Math.random() * (max - min + 1) + min) | 0
}


/**
 * 
 * @param Function fn 
 * @param Number delay 
 * @param Number atleast 
 * @return Function 
 */
// atleastdalay
const throttle = (fn, dalay, atleast = 0) => {
    let timer = null
    let previous = null
    return (...args) => {
        let now = +new Date() //
            !previous ? now : previous
        if (atleast && now - previous > atleast) {
            fn.apply(this, args)
                // 
            previous = now
            clearTimeout(timer)
        } else {
            clearTimeout(timer)
            timer = setTimeout(() => {
                fn.apply(this, args)
                previous = null
            }, dalay)
        }
    }
}

/**
 * 
 * @param Function fn 
 * @param Number delay 
 * @return Function 
 */
const debounce = (fn, dalay) => {
    let timer = null
    return (...args) => {
        if (timer) clearTimeout(timer)
        timer = setTimeout(() => {
            fn.apply(this, args)
        }, dalay);
    }
}

/**
 * 
 * @param String str 
 * @param String value 
 * @return Function 
 */
const keyWord = (str, value) => {
   const replaceReg = new RegExp(value, 'g');
       const replaceString = `<text style='color:red'>${value}</text>`
       str = str.replace(replaceReg, replaceString);
       return str
}

// 
const cloneObj = (obj) => {
    if (!obj || typeof obj !== 'object') return
    const newObj = new obj.constructor() // 
    for (const key in Object.getOwnPropertyDescriptors(obj)) { // 
        newObj[key] = cloneObj(obj[key])
    }
    return newObj
}


export default {
    chunk, //
    throttle, //
    debounce, //
    prefix, //css
    getRandomInt, //
    shuffle, //()
    keyWord, //
}